.watch {

  &__center-dot {
    width: $watch-center-dot-size;
    height: $watch-center-dot-size;
    background-color: map-get($colors, main-color);
    border-radius: 50%;

    &::before {
      @include hands(minute-hand, main-color);
    }

    &::after {
      @include hands(second-hand, secondary-color);
    }
  }
}
